/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley, .entry-content .wp-smiley, .comment-content .wp-smiley { border: none; margin-bottom: 0; margin-top: 0; padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed, iframe, object { max-width: 100%; }

img { height: auto; max-width: 100%; }

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption { margin-bottom: 1.5em; max-width: 100%; }

.wp-caption img[class*="wp-image-"] { display: block; margin-left: auto; margin-right: auto; }

.wp-caption .wp-caption-text { margin: 0.8075em 0; }

.wp-caption-text { text-align: center; }

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery { margin-bottom: 1.5em; }

.gallery-item { display: inline-block; text-align: center; vertical-align: top; width: 100%; }

.gallery-columns-2 .gallery-item { max-width: 50%; }

.gallery-columns-3 .gallery-item { max-width: 33.33%; }

.gallery-columns-4 .gallery-item { max-width: 25%; }

.gallery-columns-5 .gallery-item { max-width: 20%; }

.gallery-columns-6 .gallery-item { max-width: 16.66%; }

.gallery-columns-7 .gallery-item { max-width: 14.28%; }

.gallery-columns-8 .gallery-item { max-width: 12.5%; }

.gallery-columns-9 .gallery-item { max-width: 11.11%; }

.gallery-caption { display: block; }

/*--------------------------------------------------------------
# Theme Styles and Layout
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comments-area { margin-top: 2em; }

.comment-content a { word-wrap: break-word; }

.bypostauthor { display: block; }

/*--------------------------------------------------------------
## Posts and Pages
--------------------------------------------------------------*/
body { line-height: 1.5; }

#content { padding-top: 7rem; }

/* #content .singular, #primary .singular { padding-top: $content-top-padding; } */
article { padding-bottom: 2rem; }

.portfolio article, .team article, .has-bg article { padding-bottom: 0; }

.home #content { padding-top: 0; }

.blog #primary article p { line-height: 1.575; }

.site-content { word-break: break-word; }

.sticky { display: block; }

.byline, .updated:not(.published) { display: none; }

.single .byline, .group-blog .byline { display: inline; }

.post { margin-bottom: 4em; padding-bottom: 1em; }

.post-thumbnail { margin-bottom: 1em; }

.entry-meta, .entry-footer { font-size: .75rem; margin: .75em 0 2em; }

.entry-meta .svg-inline--fa, .entry-footer .svg-inline--fa { color: #6c757d; margin-right: .3em; }

.entry-meta span, .entry-footer span { margin: 0 1.5em .4em 0; display: inline-block; }

.entry-meta span:last-child, .entry-footer span:last-child { margin: 0; }

.entry-meta span a, .entry-footer span a { text-decoration: none; color: #6c757d; }

.entry-meta span a:hover, .entry-footer span a:hover { color: #212529; }

.entry-meta .edit-link .btn, .entry-footer .edit-link .btn { color: #ffffff; }

.entry-meta .edit-link .btn:hover, .entry-footer .edit-link .btn:hover { color: #ffffff; }

/* Sections */
/* section hr { margin-top: 3rem; margin-bottom: 3rem; } */
/* Posts Navigation */
.nav-links { display: flex; justify-content: space-between; }

/* Screenreaders */
.screen-reader-text { position: absolute; width: 1px; height: 1px; padding: 0; overflow: hidden; clip: rect(0, 0, 0, 0); white-space: nowrap; clip-path: inset(50%); border: 0; }

/* 404 page */
.error404 .content-area { position: relative; width: 100%; min-height: 1px; padding-right: 15px; padding-left: 15px; flex: 0 0 100%; max-width: 100%; }

.form-group > span { font-size: .9rem; display: inline-block; margin-bottom: 1rem; }

p > img { margin: 0 !important; }

.video-stretch video { width: 106%; height: auto; }

.resrc-block { padding: 1rem 1rem .5rem; margin: 1rem auto; border: 1px solid #f5f5f5; background-color: rgba(0, 169, 224, 0.125); }

@media (min-width: 992px) { .resrc-block { width: 80%; } }

/* .vis-design img { background-color: lighten($lt-sky-blue2, 12%); border-radius: .25rem; } .med-design img { background-color: lighten($grass, 27%); border-radius: .25rem; } */
.selected { border: 2px solid red; cursor: pointer; }

fieldset, input[type="range"] { appearance: none; }

.output { display: inline-block; position: absolute; top: 0; line-height: 2; width: 3rem; height: 2rem; margin-left: 1rem; border: 1px solid #ececec; }

.choice { display: inline-block; margin-left: 1em; color: #7a7a7a; }

#type label { margin: 1rem 0; }

#type label p { margin: 0; padding: 0; }

#type label img { padding: .35rem; }

/* fieldset label { text-align: center; width: auto; min-width: 4rem; display: inline-block; } */
.radio-wrap { position: relative; width: 15%; }

.radio-wrap input[type="radio"] { appearance: none; position: absolute; top: 0; left: 0; width: 1.5rem; height: 1.5rem; background-color: #ececec; border-radius: 50%; margin: 0; }

.checkmark { position: absolute; top: 0; left: 0; height: 1.5rem; width: 1.5rem; background-color: #eee; border-radius: 50%; }

/* On mouse-over, add a grey background color */
.radio-wrap:hover input ~ .checkmark { background-color: #ccc; }

/* When the radio button is checked, add a blue background */
.radio-wrap input:checked ~ .checkmark { background-color: #2196F3; }

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after { content: ""; position: absolute; display: none; }

/* Show the indicator (dot/circle) when checked */
.radio-wrap input:checked ~ .checkmark:after { display: block; }

/* Style the indicator (dot/circle) */
.radio-wrap .checkmark:after { top: 9px; left: 9px; width: 8px; height: 8px; border-radius: 50%; background: white; }

.totalbox { display: inline-block; text-align: center; border: 1px solid #e1e1e1; margin: 1rem; padding: 1rem; text-align: center; flex-basis: 50%; }

.totalbox .total { font-size: 2rem; color: #0085ca; }

.slider-wrap, .input-wrap { width: 100%; /* Width of the outside container */ text-align: center; }

.slider-wrap br, .input-wrap br { display: none !important; }

/* The slider itself */
.slider { appearance: none; width: 80%; /* Full-width */ height: .75rem; /* Specified height */ background: #ececec; /* Grey background */ opacity: 0.7; /* Set transparency (for mouse-over effects on hover) */ -webkit-transition: .2s; /* 0.2 seconds transition on hover */ transition: opacity .2s; padding: 0; }

/* Mouse-over effects */
.slider:hover { opacity: 1; /* Fully shown on mouse-over */ }

/* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */
.slider::-webkit-slider-thumb { -webkit-appearance: none; /* Override default look */ appearance: none; width: 25px; /* Set a specific slider handle width */ height: 25px; /* Slider handle height */ background: #0085ca; /* Green background */ cursor: pointer; /* Cursor on hover */ }

.slider::-moz-range-thumb { width: 25px; /* Set a specific slider handle width */ height: 25px; /* Slider handle height */ background: #0085ca; /* Green background */ cursor: pointer; /* Cursor on hover */ }

/*--------------------------------------------------------------
## Widgets
--------------------------------------------------------------*/
.widget { margin: 0 0 2em; /* Make sure select elements fit in widgets. */ }

.widget select { max-width: 100%; }

.widget #wp-calendar { width: 100%; }

.widget .card-header { font-size: 1.25rem; }

.widget .tagcloud, .widget .textwidget, .widget .calendar_wrap { padding: 1.25rem; }

.widget ul { padding-top: 0.75rem; padding-bottom: 0.75rem; margin-bottom: 0; }

.widget.widget_search { border: none; }

.widget .tagcloud a { margin-bottom: .5rem; }

.widget ul { padding-top: 1rem; padding-bottom: 1.25rem; }

/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
nav { padding: 1rem; background: white; }

@media (min-width: 768px) { nav { box-shadow: 0 8px 3px -3px rgba(0, 0, 0, 0.05); } }

nav.navbar { padding: .75rem 1rem; }

nav.fixed-top { z-index: 2000; }

.navbar-light .navbar-nav .nav-link { color: #7a7a7a; font-size: .8rem; }

@media (max-width: 991.98px) { .navbar-toggler-right { margin-left: 90%; position: relative; }
  .navbar-light .navbar-toggler { border: none; }
  .nav { background: none; }
  .navbar-nav { text-align: center; padding-bottom: 2rem; box-shadow: 0 5px 8px 3px rgba(0, 0, 0, 0.05); } }

@media (max-width: 991.98px) and (min-width: 992px) { .navbar-nav { margin-left: 10.5rem; } }

@media (max-width: 991.98px) and (min-width: 1200px) { .navbar-nav { margin-left: 8rem; } }

@media (max-width: 767.98px) { .navbar-toggler-right { margin-left: 85%; } }

.navbar-brand { position: absolute; top: 0; background: white; border-radius: 0.35rem; padding: 0; width: 9rem; }

.navbar-brand.scaled { width: 6rem; box-shadow: none; transition: width 500ms ease-in-out; }

@media (min-width: 768px) { .navbar-brand { box-shadow: 0 8px 3px -3px rgba(0, 0, 0, 0.05); } }

.navbar-brand a { display: block; overflow: hidden; }

.nav-item { margin: 0 1.75rem; }

@media (min-width: 992px) { .nav-item { margin: 0 1.25rem; } }

@media (min-width: 1200px) { .nav-item { margin: 0 1.5rem; } }

.nav-item a { text-transform: uppercase; }

.nav-item.btn-primary { border-radius: 0.35rem; padding-left: 1rem; padding-right: 1rem; }

.nav-item.btn-primary a { color: white !important; }

/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
html { position: relative; min-height: 100%; }

body { padding-bottom: 5rem; /* Margin bottom by footer height */ }

@media (max-width: 575.98px) { body { padding-bottom: 10rem; } }

.site-footer { position: absolute; z-index: 1600; bottom: 0; width: 100%; height: auto; /* Set the fixed height of the footer here */ /* line-height: 60px; */ /* Vertically center the text there */ min-height: 5vh; height: auto; background-color: #f5f5f5; padding: 1rem 0; }

@media (max-width: 575.98px) { .footer-logo { width: 50%; } }

@media (max-width: 575.98px) { .site-info { text-align: center !important; margin-top: 1.5rem; } }

