# Content on Left with Sidebar on Right
--------------------------------------------------------------*/
.content-area { position: relative; width: 100%; min-height: 1px; padding-right: 15px; padding-left: 15px; }

@media (min-width: 576px) { .content-area { flex: 0 0 100%; max-width: 100%; } }

@media (min-width: 768px) { .content-area { flex: 0 0 66.66667%; max-width: 66.66667%; } }

