@media (min-width: 768px) { .widget-area { flex: 0 0 33.33333%; max-width: 33.33333%; } }

/*--------------------------------------------------------------
# CStudio Specific
--------------------------------------------------------------*/
html { font-size: 14px; }

@media screen and (min-width: 320px) { html { font-size: calc(14px + 4 * ((100vw - 320px) / 880)); } }

@media screen and (min-width: 1200px) { html { font-size: 18px; } }

