h1 { letter-spacing: 0.025em; font-size: 2.0736rem; }

h1 { font-size: 24.192px; }

@media screen and (min-width: 320px) { h1 { font-size: calc(24.192px + 23.058 * ((100vw - 320px) / 880)); } }

@media screen and (min-width: 1200px) { h1 { font-size: 47.25px; } }

h2 { font-size: 1.728rem; }

h2 { font-size: 24.192px; }

@media screen and (min-width: 320px) { h2 { font-size: calc(24.192px + 23.058 * ((100vw - 320px) / 880)); } }

@media screen and (min-width: 1200px) { h2 { font-size: 47.25px; } }

h3 { font-size: 1.44rem; }

h3 { font-size: 20.16px; }

@media screen and (min-width: 320px) { h3 { font-size: calc(20.16px + 11.34 * ((100vw - 320px) / 880)); } }

@media screen and (min-width: 1200px) { h3 { font-size: 31.5px; } }

h2, .h2, h3, .h3, h4, .h4, h5, .h5 { letter-spacing: 0.025em; font-family: "Open Sans", Helvetica, Arial, sans-serif; font-weight: 300 !important; }

.lead { font-size: 1.25rem; font-weight: 300; }

.sans-serif, .open-sans-light { font-family: "Open Sans", Helvetica, Arial, sans-serif; font-weight: 300 !important; }

.blockquote { font-weight: 300; line-height: 1.5; padding: 0 1.5rem; position: relative; margin: 1rem 0; }

.blockquote p { margin-left: 1em; }

.blockquote.quoted:before { content: ""; width: 2.5rem; height: 2.5rem; background-image: url("../imgs/quote.svg"); background-repeat: no-repeat; background-position: center center; background-size: 100%; background-repeat: no-repeat; opacity: .175; position: absolute; top: -1rem; left: -.25rem; display: inline-block; transform: scaleY(-1); }

.blockquote.quoted:after { content: ""; width: 2rem; height: 2rem; background-image: url("../imgs/quote.svg"); background-repeat: no-repeat; background-position: center center; background-size: 100%; background-repeat: no-repeat; opacity: .175; position: absolute; bottom: .35rem; right: 3.5rem; display: inline-block; transform: scale(-1, -1); filter: FlipH; -ms-filter: "FlipH"; }

