[ Input ]*/
input, textarea { outline: none; border: none; padding: 1rem; border-radius: 4px; width: 100%; margin: .5rem 0 1.25rem; background-color: white; }

input[aria-required='true'], textarea[aria-required='true'] { border-left: 5px solid #ff3f3f; }

input.form-control { margin: 0; }

/* textarea { outline: none; border: none; } */
textarea:focus, input:focus { border-color: transparent !important; }

select { border: none; }

